  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

#root,
#__next {
  isolation: isolate;
}

/* My CSS */

:root {
  --milky-color: rgb(229, 229, 203);
  --dim-milky-color: rgb(229, 229, 203, 0.8);
  --cream-color: rgb(213, 206, 163);
  --brown-color: rgb(60, 42, 33);
  --dim-brown-color: rgb(60, 42, 33, 0.8);
  --dark-brown-color: rgb(26, 18, 11);
  --required-red-color: rgb(204, 37, 37);
}

svg {
  display: grid;
  padding: 0;
  margin: 0;
  place-items: center;
}

button,
input[type="text"],
textarea {
  appearance: none;
}

.checkbox-label {
  margin-top: 2px;
}

input[type="checkbox"] {
  appearance: none;
  margin: 0;
  font: inherit;
  color: currentColor;
  width: 1.15em;
  height: 1.15em;
  border: 0.15em solid var(--brown-color);
  border-radius: 0.15em;
  transform: translateY(-0.075em);
  display: grid;
  place-content: center;
  border-radius: 50%;
}

input[type="checkbox"]::before {
  content: "";
  width: 0.65em;
  height: 0.65em;
  transform: scale(0);
  transition: 120ms transform ease-in-out;
  box-shadow: inset 1em 1em var(--brown-color);
  border-radius: 50%;
}

input[type="checkbox"]:checked::before {
  transform: scale(1);
}

input[type="checkbox"]:hover {
  cursor: pointer;
}

.container {
  height: 100vh;
  display: grid;
  grid-template-columns: minmax(300px, 1fr) minmax(500px, 4fr);
}

.sidebar {
  background-color: var(--dark-brown-color);
  display: grid;
  grid-template-rows: minmax(80px, 100px) minmax(500px, 1fr);
  padding: 1em;
}

.logo-container {
  display: grid;
  grid-template-columns: minmax(50px, 65px) minmax(150px, 1fr);
  gap: 1em;
  place-items: center;
}

.logo {
  fill: var(--milky-color);
  width: 4rem;
}

.logo-text {
  color: var(--milky-color);
  font-size: 4rem;
  font-weight: bold;
  justify-self: start;
}

.menu-container {
  margin-top: 10vh;
  display: grid;
  grid-template-rows: minmax(25px, 50px) minmax(500px, 1fr);
  gap: 1em;
}

.projects-label {
  display: grid;
  grid-template-columns: minmax(100px, 3fr) minmax(10px, 30px);
  align-items: center;
  border-bottom: 1px solid var(--cream-color);
}

.projects-title {
  color: var(--milky-color);
}

.project-add-icon {
  fill: var(--dark-brown-color);
  background-color: var(--milky-color);
  border-radius: 8px;
  border: 2px solid var(--dark-brown-color);
  padding: 1px;
}

.project-add-icon:hover {
  border: none;
  background-color: var(--cream-color);
  cursor: pointer;
}

dialog {
  padding: 1em;
  background-color: var(--cream-color);
  margin: 10vh auto;
  border: 3px solid var(--dark-brown-color);
}

dialog::backdrop {
  background-color: var(--milky-color);
  opacity: 0.8;
}

dialog input[type="text"],
dialog textarea {
  border-radius: 5px;
  border: 1px solid var(--brown-color);
}

dialog input[type="text"].invalid,
dialog textarea.invalid {
  border: 2px solid var(--required-red-color);
}

.project-dialog-container {
  display: grid;
  grid-template-rows: minmax(30px, 40px) auto;
  gap: 1em;
}

.project-add-title {
  color: var(--dark-brown-color);
}

.project-add-form {
  display: grid;
  grid-template-columns: minmax(80px, 1fr) minmax(200px, 3fr);
  gap: 0.5em;
}

.project-dialog-buttons {
  grid-column: span 2;
  justify-self: end;
}

.required {
  color: var(--required-red-color);
}

.project-dialog-button {
  width: 80px;
  background-color: var(--milky-color);
  color: var(--dark-brown-color);
  border-radius: 8px;
  border: 1px solid var(--dark-brown-color);
}

.project-dialog-button:hover {
  background-color: var(--dark-brown-color);
  color: var(--cream-color);
  font-weight: 600;
}

.projects,
.default-projects {
  display: grid;
  align-items: center;
  grid-auto-rows: min-content;
  gap: 0.5em 1em;
}

.default-projects {
  padding-bottom: 1em;
  margin-bottom: 1em;
  border-bottom: 1px solid var(--cream-color);
}

.projects {
  grid-template-columns: minmax(100px, 3fr) minmax(20px, 30px);
}

.project {
  background-color: var(--milky-color);
  color: var(--dark-brown-color);
  border-radius: 5px;
  border: 1px solid var(--milky-color);
  cursor: pointer;
}

.project:hover {
  background-color: var(--cream-color);
  font-weight: bold;
  border-color: var(--cream-color);
}

.project-delete-icon {
  fill: var(--dim-milky-color);
}

.project-delete-icon:hover {
  fill: var(--milky-color);
  opacity: 1;
}

.main-content {
  background-color: var(--cream-color);
  display: grid;
  grid-template-rows: minmax(40px, 60px) minmax(500px, 1fr);
  padding: 1em;
  grid-template-columns: minmax(500px, 60vw);
  justify-content: center;
}

.heading-container {
  display: grid;
  grid-template-columns: minmax(500px, 1fr) minmax(25px, 30px);
  border-bottom: 1px solid var(--dark-brown-color);
  align-items: center;
}

.heading {
  color: var(--brown-color);
}

.item-add-icon {
  fill: var(--cream-color);
  background-color: var(--brown-color);
  border-radius: 8px;
  padding: 1px;
  border: 1px solid var(--cream-color);
}

.item-add-icon:hover {
  background-color: var(--dark-brown-color);
  border: none;
  cursor: pointer;
}

.item-add-icon.disabled {
  opacity: 0.7;
}

.item-add-icon.disabled:hover {
  background-color: var(--brown-color);
  border: 1px solid var(--cream-color);
}

.item-dialog-container {
  display: grid;
  grid-template-rows: minmax(20px, 40px) min-content;
  gap: 1em;
}

.item-add-form {
  display: grid;
  grid-template-columns: minmax(150px, 1fr) minmax(300px, 3fr);
  grid-template-rows: minmax(20px, 30px) minmax(50px, 80px) repeat(
      2,
      minmax(20px, 30px)
    );
  gap: 0.5em;
}

.item-add-title {
  border-bottom: 1px solid var(--dark-brown-color);
}

.item-description-field {
  resize: none;
}

.item-dialog-buttons {
  grid-column: span 2;
  justify-self: end;
  display: flex;
  gap: 0.5em;
}

.item-dialog-button {
  width: 80px;
  background-color: var(--milky-color);
  color: var(--dark-brown-color);
  border-radius: 8px;
  border: 1px solid var(--dark-brown-color);
}

.item-dialog-button:hover {
  background-color: var(--dark-brown-color);
  color: var(--cream-color);
  font-weight: 600;
}

.todo-items {
  display: grid;
  grid-auto-rows: minmax(50px, 60px);
  gap: 0.5em;
  padding-top: 1em;
  padding-left: 8px;
}

.todo-item {
  box-shadow: -8px 0 0 var(--brown-color);
  border-radius: 8px;
  background-color: var(--milky-color);
  padding: 0.5em 1em;
  display: grid;
  grid-template-areas:
    "check title delete"
    ". duedate .";
  justify-items: start;
  grid-template-columns: minmax(10px, 20px) minmax(100px, 1fr) minmax(
      20px,
      22px
    );
  grid-template-rows: minmax(20px) minmax(10px);
  align-items: center;
  gap: 0 0.5em;
}

input[type="checkbox"] {
  margin: 0;
  grid-area: check;
  /* appearance: none;
  color: var(--dark-brown-color);
  width: 1.15em;
  height: 1.15em;
  border: 1.5px solid var(--dark-brown-color);
  border-radius: 50%; */
  display: grid;
  place-content: center;
}

