.item-title {
  font-weight: bold;
  font-size: 1.2rem;
  color: var(--dark-brown-color);
  grid-area: title;
  margin-top: -2px;
  display: flex;
  align-items: center;
}

.item-priority {
  font-weight: normal;
  font-size: 0.8em;
  margin-left: 0.5em;
}

.item-project {
  font-weight: normal;
  font-size: 0.6em;
  margin-left: 1em;
}

.item-project::before {
  content: "(";
}

.item-project::after {
  content: ")";
}

.item-due-date {
  margin-top: -5px;
  grid-area: duedate;
  font-size: 0.95em;
}

.item-delete-icon {
  grid-area: delete;
  fill: var(--dim-brown-color);
}

.item-delete-icon:hover {
  fill: var(--brown-color);
}

.item-delete-icon.disabled {
  opacity: 0.7;
}

.project-change-button,
.item-change-button {
  display: none;
