-rows: minmax(25px, 50px) minmax(500px, 1fr);\n  gap: 1em;\n}\n\n.projects-label {\n  display: grid;\n  grid-template-columns: minmax(100px, 3fr) minmax(10px, 30px);\n  align-items: center;\n  border-bottom: 1px solid var(--cream-color);\n}\n\n.projects-title {\n  color: var(--milky-color);\n}\n\n.project-add-icon {\n  fill: var(--dark-brown-color);\n  background-color: var(--milky-color);\n  border-radius: 8px;\n  border: 2px solid var(--dark-brown-color);\n  padding: 1px;\n}\n\n.project-add-icon:hover {\n  border: none;\n  background-color: var(--cream-color);\n  cursor: pointer;\n}\n\ndialog {\n  padding: 1em;\n  background-color: var(--cream-color);\n  margin: 10vh auto;\n  border: 3px solid var(--dark-brown-color);\n}\n\ndialog::backdrop {\n  background-color: var(--milky-color);\n  opacity: 0.8;\n}\n\ndialog input[type=\"text\"],\ndialog textarea {\n  border-radius: 5px;\n  border: 1px solid var(--brown-color);\n}\n\ndialog input[type=\"text\"].invalid,\ndialog textarea.invalid {\n  border: 2px solid var(--required-red-color);\n}\n\n.project-dialog-container {\n  display: grid;\n  grid-template-rows: minmax(30px, 40px) auto;\n  gap: 1em;\n}\n\n.project-add-title {\n  color: var(--dark-brown-color);\n}\n\n.project-add-form {\n  display: grid;\n  grid-template-columns: minmax(80px, 1fr) minmax(200px, 3fr);\n  gap: 0.5em;\n}\n\n.project-dialog-buttons {\n  grid-column: span 2;\n  justify-self: end;\n}\n\n.required {\n  color: var(--required-red-color);\n}\n\n.project-dialog-button {\n  width: 80px;\n  background-color: var(--milky-color);\n  color: var(--dark-brown-color);\n  border-radius: 8px;\n  border: 1px solid var(--dark-brown-color);\n}\n\n.project-dialog-button:hover {\n  background-color: var(--dark-brown-color);\n  color: var(--cream-color);\n  font-weight: 600;\n}\n\n.projects,\n.default-projects {\n  display: grid;\n  align-items: center;\n  grid-auto-rows: min-content;\n  gap: 0.5em 1em;\n}\n\n.default-projects {\n  padding-bottom: 1em;\n  margin-bottom: 1em;\n  border-bottom: 1px solid var(--cream-color);\n}\n\n.projects {\n  grid-template-columns: minmax(100px, 3fr) minmax(20px, 30px);\n}\n\n.project {\n  background-color: var(--milky-color);\n  color: var(--dark-brown-color);\n  border-radius: 5px;\n  border: 1px solid var(--milky-color);\n  cursor: pointer;\n}\n\n.project:hover {\n  background-color: var(--cream-color);\n  font-weight: bold;\n  border-color: var(--cream-color);\n}\n\n.project-delete-icon {\n  fill: var(--dim-milky-color);\n}\n\n.project-delete-icon:hover {\n  fill: var(--milky-color);\n  opacity: 1;\n}\n\n.main-content {\n  background-color: var(--cream-color);\n  display: grid;\n  grid-template-rows: minmax(40px, 60px) minmax(500px, 1fr);\n  padding: 1em;\n  grid-template-columns: minmax(500px, 60vw);\n  justify-content: center;\n}\n\n.heading-container {\n  display: grid;\n  grid-template-columns: minmax(500px, 1fr) minmax(25px, 30px);\n  border-bottom: 1px solid var(--dark-brown-color);\n  align-items: center;\n}\n\n.heading {\n  color: var(--brown-color);\n}\n\n.item-add-icon {\n  fill: var(--cream-color);\n  background-color: var(--brown-color);\n  border-radius: 8px;\n  padding: 1px;\n  border: 1px solid var(--cream-color);\n}\n\n.item-add-icon:hover {\n  background-color: var(--dark-brown-color);\n  border: none;\n  cursor: pointer;\n}\n\n.item-add-icon.disabled {\n  opacity: 0.7;\n}\n\n.item-add-icon.disabled:hover {\n  background-color: var(--brown-color);\n  border: 1px solid var(--cream-color);\n}\n\n.item-dialog-container {\n  display: grid;\n  grid-template-rows: minmax(20px, 40px) min-content;\n  gap: 1em;\n}\n\n.item-add-form {\n  display: grid;\n  grid-template-columns: minmax(150px, 1fr) minmax(300px, 3fr);\n  grid-template-rows: minmax(20px, 30px) minmax(50px, 80px) repeat(\n      2,\n      minmax(20px, 30px)\n    );\n  gap: 0.5em;\n}\n\n.item-add-title {\n  border-bottom: 1px solid var(--dark-brown-color);\n}\n\n.item-description-field {\n  resize: none;\n}\n\n.item-dialog-buttons {\n  grid-column: span 2;\n  justify-self: end;\n  display: flex;\n  gap: 0.5em;\n}\n\n.item-dialog-button {\n  width: 80px;\n  background-color: var(--milky-color);\n  color: var(--dark-brown-color);\n  border-radius: 8px;\n  border: 1px solid var(--dark-brown-color);\n}\n\n.item-dialog-button:hover {\n  background-color: var(--dark-brown-color);\n  color: var(--cream-color);\n  font-weight: 600;\n}\n\n.todo-items {\n  display: grid;\n  grid-auto-rows: minmax(50px, 60px);\n  gap: 0.5em;\n  padding-top: 1em;\n  padding-left: 8px;\n}\n\n.todo-item {\n  box-shadow: -8px 0 0 var(--brown-color);\n  border-radius: 8px;\n  background-color: var(--milky-color);\n  padding: 0.5em 1em;\n  display: grid;\n  grid-template-areas:\n    \"check title delete\"\n    \". duedate .\";\n  justify-items: start;\n  grid-template-columns: minmax(10px, 20px) minmax(100px, 1fr) minmax(\n      20px,\n      22px\n    );\n  grid-template-rows: minmax(20px) minmax(10px);\n  align-items: center;\n  gap: 0 0.5em;\n}\n\ninput[type=\"checkbox\"] {\n  margin: 0;\n  grid-area: check;\n  /* appearance: none;\n  color: var(--dark-brown-color);\n  width: 1.15em;\n  height: 1.15em;\n  border: 1.5px solid var(--dark-brown-color);\n  border-radius: 50%; */\n  display: grid;\n  place-content: center;\n}\n\n/* input[type=\"checkbox\"]::before {\n  content: \"\";\n  width: 0.65em;\n  height: 0.65em;\n  transform: scale(0);\n  transition: 120ms transform ease-in-out;\n  box-shadow: inset 1em 1em var(--dark-brown-color);\n}\n\ninput[type=\"checkbox\"]:checked::before {\n  transform: scale(1);\n} */\n\n.item-title {\n  font-weight: bold;\n  font-size: 1.2rem;\n  color: var(--dark-brown-color);\n  grid-area: title;\n  margin-top: -2px;\n  display: flex;\n  align-items: center;\n}\n\n.item-priority {\n  font-weight: normal;\n  font-size: 0.8em;\n  margin-left: 0.5em;\n}\n\n.item-project {\n  font-weight: normal;\n  font-size: 0.6em;\n  margin-left: 1em;\n}\n\n.item-project::before {\n  content: \"(\";\n}\n\n.item-project::after {\n  content: \")\";\n}\n\n.item-due-date {\n  margin-top: -5px;\n  grid-area: duedate;\n  font-size: 0.95em;\n}\n\n.item-delete-icon {\n  grid-area: delete;\n  fill: var(--dim-brown-color);\n}\n\n.item-delete-icon:hover {\n  fill: var(--brown-color);\n}\n\n.item-delete-icon.disabled {\n  opacity: 0.7;\n}\n\n.project-change-button,\n.item-change-button {\n  display: none;\n}"],"sourceRoot":""}]);
